<script lang="ts">;
import Options from "./Options.svelte";
import Reveal from "./Reveal.svelte";
let mode = "options";
let options = {};
</script>

<style>
  main {
    height: 100%;
  }
</style>

<main>
  {#if mode === 'options'}
    <Options
      initialOptions={{}}
      on:submit={(event) => {
        options = event.detail;
        mode = 'reveal';
      }} />
  {:else}
    <Reveal
      {options}
      on:finished={() => {
        mode = 'options';
      }} />
  {/if}
</main>
